import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import FetchData from '@/components/FetchData'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [    
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/FetchData',
      name: 'FetchData',
      component: FetchData
    }
  ]
})



